/* carousel */
:root {
  --widths: 100%;
}

.slider-container {
  width: 100%;
  width: var(--widths);
  height: 220px;
  margin: auto;

  z-index: 2;
}

.active {
  display: inline-block;
}

.inactive {
  display: none;
}

.slides {
  width: 100%;
  width: var(--widths);
  position: relative;
}

.slide-image {
  width: 100%;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left 40%,
    left bottom,
    from(rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );
          mask-image: -webkit-gradient(
    linear,
    left 40%,
    left bottom,
    from(rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );

  object-fit: fill;
}

.prev,
.next {
  cursor: pointer;
  z-index: 3;
  position: absolute;
  top: 20%;
  width: auto;
  padding: 1rem;

  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.prev:hover,
.next:hover {
  color: white;

  transition: all 0.2s ease-in;
}

.next {
  right: 0;
  border-radius: 5px 0px 0px 5px;
}

@media screen and (max-width: 675px) {
  .home__row {
    display: flex;
    flex-wrap: wrap;
  }

  .slider-container {
    height: 120px;
  }
  .prev,
  .next {
    position: absolute;
    top: 10%;

    font-size: 30px;
  }
  .slide-image {
    -webkit-mask-image: -webkit-gradient(
      linear,
      left 70%,
      left bottom,
      from(rgba(0, 0, 0, 1)),
      to(rgba(0, 0, 0, 0))
    );
            mask-image: -webkit-gradient(
      linear,
      left 70%,
      left bottom,
      from(rgba(0, 0, 0, 1)),
      to(rgba(0, 0, 0, 0))
    );
  }
  .slide-image {
    height: 150px;
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin: 10px;
  padding: 15px;
  width: 100%;

  flex: 1 1 150px;
  min-width: 100px;
  background-color: white;
  z-index: 5;
  transition: 0.2s;
  border-radius: 2px;
}
.product:hover {
  box-shadow: 5px 10px 20px rgb(167, 212, 255);
}

.product__rating {
  display: flex;
}

.product > img {
  height: 170px;
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 1px;
}

.fbutton {
  background: #f8de5a;
  border: 1px solid;
  margin-top: 6px;
  padding-left: 2px;
  padding-right: 2px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  cursor: pointer;
  border-radius: 3px;
}
.fbutton:focus {
  color: rgb(70, 70, 70);
  padding-left: 4px;
  padding-right: 4px;
}
.fbutton:hover {
  background: #ffe877;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media screen and (max-width: 675px) {
  .product__price {
    margin-top: 2px;
  }
  .product__info {
    margin-bottom: 0px;
  }
  .fbutton {
    margin-top: 4px;
  }
  .product__rating {
    font-size: small;
  }
  .product > img {
    height: 160px;
  }
}

.slidersection {
  transition: 0.5s;
}

.checkout {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  background-color: white;
  height: -webkit-max-content;
  height: max-content;
  margin-bottom: 35px;
}
.checkout__ad {
  width: 100%;
