}
.checkout__title {
  margin-right: 10px;
  margin-bottom: 5px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}
.user__name {
  text-transform: capitalize;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin-left: 10px;
  font-size: large;
}
@media screen and (max-width: 675px) {
